import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from '../../../http/custom-operation/custom-operation.http.service';
import { Observable } from 'rxjs/Rx';
import { GroupGradeInfoBean } from './../../bean/custom-group.bean';
import { SelectItem, Message, ConfirmationService, ConfirmDialogModule } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';

@Component({
  selector: 'customGroup-rating-approval2',
  templateUrl: './customGroup-rating-approval2.html',
  styleUrls: ['./customGroup-rating-approval2.scss'],
  providers: [ConfirmationService, CusOperationHttpService],

})
export class GroupApproval2Component implements OnInit {

  public cunstomGroupName: string = "XXX";
  public cunstomRatingAdjust: string = "";
  public cunstomRating: string = "";
  public msgs: Message[] = [];  //
  public groupGradeInfoBean: GroupGradeInfoBean = new GroupGradeInfoBean();// 
  public adjustGroupLevelSel: string;//
  public rateFlag: boolean = true;//
  public selGroupLevel: any;//
  // 
  // public CODEVALUE: any =  CODEVALUE;
  // 
  sysGroupLevel: SelectItem[] = [];//
  groupLevel: SelectItem[] = [];//
  adjustGroupLevel: SelectItem[] = [];//

  groupLevel2: string; //
  // 
  public groupId: any = '';//
  public groupName: any = '';//
  public params: any;

  constructor(
    public httpService: CusOperationHttpService,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
  ) { }

  ngOnInit() {
    // 
    // route
    this.groupId = this.routerInfo.snapshot.params.groupId;//    groupLevel  
    this.groupLevel2 = this.routerInfo.snapshot.params.groupLevel;//    groupLevel  
    // ,
    this.groupLevel = [
      { label: '', value: '' },
      { label: '', value: '1' },
      { label: '', value: '2' },
      { label: '', value: '3' },
      { label: '', value: '4' },
      { label: '', value: '5' },
    ];
    // 
    this.sysGroupLevel = [
      { label: '', value: '' },
      { label: '', value: '1' },
      { label: '', value: '2' },
      { label: '', value: '3' },
      { label: '', value: '4' },
      { label: '', value: '5' },
    ];
    //  // 
    this.adjustGroupLevel = [
      { label: '', value: '' },
      { label: '', value: '1' },
      { label: '', value: '2' },
      { label: '', value: '3' },
      { label: '', value: '4' },
      { label: '', value: '5' },
    ];
    // this.params = this.routerInfo.snapshot.params;
    // this.preSet();
    this.selectGroupGradeInfo();
    this.selectCustGroupInfo();
  }
  preSet() {

    // 
    this.selectGroupGradeInfo();
    // this.rateFlag = false;
  }
  selectGroupGradeInfo() {
    //  
    this.httpService.selectGroupGradeInfo({ groupId: this.groupId }).subscribe(data => {
      if (data.returnCode.code == 'success') {
        //
        this.groupGradeInfoBean = data;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }]
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }

  selectCustGroupInfo() {
    let params = {
      groupId: this.groupId
    }
    // 
    this.httpService.selectCustGroupInfo(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupName = data.groupName;
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '';
        this.msgs = [{ severity: 'error', summary: '', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs = [{ severity: 'error', summary: '', detail: '' }];
    })
  }
}
